header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 40px 0;
  z-index: 1000;
}

header .name {
  text-decoration: none;
  font-size: 24px;
  letter-spacing: .1em;
  position: relative;
  font-family: futura_r;
}

header .name:hover {
  opacity: .8;
  color: #fff;
}

.fa,
.navList a,
.name,
.icons {
  color: #fff;
}

.fa {
  margin-left: 2rem;
  display: inline-block;
  font-size: 2rem;
}

.menuIcon {
  cursor: pointer;
  width: 50px;
  height: 22px;
  position: relative;
  border: 1px solid black;
}

.navList {
  color: #fff;
  display: none;
}

.ship {
  width: 25%;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  width: 100%;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

li {
  list-style-type: none;
}

* a {
  text-decoration: none;
}

.bkgColor {
  background: url("../img/background.svg") 50% 50%;
  background-size: cover;
  z-index: -100;
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}
